$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  --mt-titulo-resumo-padding: 4px 0 0 0;
  --mt-titulo-resumo-margin: 0;

  color: #9abec0;
  font-family: 'Roboto', sans-serif;
  font-weight: 900;
  font-size: 14px;
  line-height: 1;
  margin: var(--mt-titulo-resumo-margin);
  padding: var(--mt-titulo-resumo-padding);
  letter-spacing: 1px;
}
